@import '~antd/dist/antd.css';

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.ant-layout {
  justify-content: space-between;
  min-height: 100vh;
  background-color: #fff;
  position: relative;
 
}

.ant-layout-content{
  display: flex;
  flex-direction: column;
  align-items: center;
position: relative;
}
.app-background{
  position: absolute;
width: 100%;


min-height: 550px;
  left: 0;
  bottom: 0;
  background-image: url(./2.4_example_1.png);
  background-repeat: no-repeat;
  background-size: contain;
  
}
.app-flex{
  display: flex;
  width: 50%;
  margin-bottom: 40px;
  height: 40px;
}
.ant-layout-header,
.ant-layout-footer{
  background-color: #aedafa;
}
.ant-layout-footer{
  padding: 10px 0;
  box-shadow: 0 -16px 10px 2px #aedafa, 0 -6px 30px 5px #aedafa, 0 -8px 10px 7px #aedafa;
  
}
.ant-layout-header{
  height: 15vh;
  box-shadow: 0 16px 10px 2px #aedafa, 0 6px 30px 5px #aedafa, 0 8px 10px -7px #aedafa;
}
h1.ant-typography,
h2.ant-typography{
  text-align: center;
  font-style: italic;
  color: #fff;
  margin: 0;
}

.ant-typography + h2.ant-typography{
  margin: 0;
}

.ant-input{
  border-color: black;
  border-radius: 15px 0px 0px 15px;
}
.ant-btn.ant-btn-primary.ant-btn-lg{
  border-radius: 0 15px 15px 0;
}

.ant-list{
  width: 450px;
  max-width: 100%;
  background-color: #fff;
  margin-bottom: 40px;
}
.ant-list-split .ant-list-header {
  border-bottom: 1px solid black;
}
.ant-list-split .ant-list-footer {
  border-top: 1px solid black;
}
.ant-list-bordered{
  border: 1px solid black
}
.ant-card-body{
 
 min-height: 300px;
  padding: 4px;
}
.ant-card-bordered {
  border: 1px solid black;
}
.ant-card-head{
  border-bottom: 1px solid black
}
.ant-card{
  margin: 8px;
}

.ant-list-bordered.ant-list-lg .ant-list-item {
  height: 45px;
  padding: 0px 10px 0px 25px;
}
.anticon.anticon-check-circle{
  margin-left: 20px;
  font-size: 20px;
  color: #69c0ff;
  transition: all .2s ease-in;
  cursor: pointer;
}
.anticon.anticon-check-circle.active{
  margin-left: 20px;
  font-size: 23px;
  color: #289258;
  transition: all .2s ease-in;
  cursor: pointer;
}
.anticon.anticon-check-circle:hover{
  color: #289258;
  font-size: 23px;
}

.anticon.anticon-delete{
  margin-left: 10px;
  font-size: 20px;
  color: #69c0ff;
  transition: all .2s ease-in;
  cursor: pointer;
}
.anticon.anticon-delete:hover{
  color: #ff4d4f;
  font-size: 23px;
}

.anticon.anticon-sort-ascending{
  color: #69c0ff;
  font-size: 20px;
  margin-left: 5px;
  cursor: pointer;
  transition: all .2s ease-in;

}
.anticon.anticon-sort-ascending:hover{
  font-size: 25px;
}

.app-wrap{
   display: flex;
   justify-content: space-between;
}

.done{
  text-decoration: line-through;
}
.mr-2{
  margin-right: 20px;
}
.mr-3{
  margin-right: 30px;
}
.ml-3{
  margin-left: 30px;
}
.ant-btn.ant-btn-background-ghost{
  transition: all .5s ease-in;
  transform: skewX(-8deg);
}
.ant-btn.ant-btn-background-ghost.app-btn-classic{
  border-color: #ff4d4f;
  color: #ff4d4f;
  border-color: #13e400;
  color: #13e400;
  transition: all .5s ease-in;
}
.ant-btn.ant-btn-background-ghost.app-btn-drag{
  border-color: #ff4d4f;
  color: #ff4d4f;
  transition: all .5s ease-in;
}


.anticon.anticon-sync{
transition: all .5s ease-in;

}
.anticon.anticon-sync.mode{
  transform: rotate(340deg);
  color: #13e400;
  }
.anticon.anticon-sync.not-mode{
    transform: rotate(0deg);
   
    color: #ff4d4f;
}

.todo-drag-item{
  display: flex;
  justify-content: space-between;
  align-items: center;
  user-select: none;
  border: 1px solid black;
  height: 40px;
  padding: 0 15px;
  margin: 0 0 8px 0;
  min-height: 50px;
 
  color: black;
}